import React, { useState, useEffect } from "react";
import { getBannerAPI, getPlaylistHotAPI } from "../../axios/api";
// swiper
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "./HomePage.css";

export default function HomePage() {
  const [BannerList, setBannerList] = useState([]);
  const [TagList, setTagList] = useState([]);
  const getBannerList = () => {
    const params = {
      type: 0,
    };
    getBannerAPI(params).then((res) => {
      setBannerList(res.banners);
    });
  };
  const getPlayList = () => {
    getPlaylistHotAPI().then((res) => {
      setTagList(res.tags);
    });
  };
  useEffect(() => {
    // console.log("ajaxdom")
    getBannerList();
    getPlayList();
  }, []);
  return (
    <div className="content">
      {/*  */}
      <Swiper
        autoplay={{
          delay: 3000, // 3s
          stopOnLastSlide: false, // 
          disableOnInteraction: true, //  
        }}
        spaceBetween={30}
        loop={true}
        slidesPerView={3}
        onClick={() => {
          console.log("click");
        }}
      >
        {BannerList.map((item) => {
          return (
            <SwiperSlide key={item.imageUrl}>
              <img className="bannerImg" src={item.imageUrl} alt="#"></img>
            </SwiperSlide>
          );
        })}
      </Swiper>
      {/*  */}
      <div className="content_main">
        {TagList.map((item) => {
          return <span className="content_main_text">{item.name}</span>;
        })}
      </div>
    </div>
  );
}
